.about-us {
  @include pageGrid;

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column:  1 / 4 ;
    }

    @include onDesktop {
      grid-column:  span 5 ;
    }
  }

  &__info {
    margin: 0;
    grid-column:  1 / -1;
    font-size: 23px;
    font-weight: 500;

    @include onTablet {
      grid-column: 4 / -1;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }
}
