.details {

  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }

    &-btn {
      @include pageGrid;
    }
  }

  &__btn {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 / -2;
    }

    @include onDesktop {
      grid-column: 5 / 9;
    }
  }
}

.detail {
  &__link {
    position: relative;
    overflow: hidden;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photos {
    @include pageGrid;

    margin-bottom: 16px;
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include onDesktop {
      @include hover(transform, scale(1.05));
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
  }
}
